































































import { Component, Vue } from 'vue-property-decorator';
import { CertOwn} from '@/entry/diamond';
import {Row,Col,NoticeBar,Button,Divider} from 'vant';
import {notes, NoteType} from '@/note';

import'vant/lib/button/style';
import'vant/lib/col/style';
import'vant/lib/notice-bar/style';
import'vant/lib/row/style';
import'vant/lib/divider/style';


Vue.use(Row).use(Col).use(NoticeBar).use(Button).use(Divider);

@Component
export default class Confirm extends Vue {
    private certOwn!:CertOwn;
    private oShowConfirm = true;
    private oShowSign = false;
    private ownSign='';

    public created(): void {
        this.certOwn = this.$store.state.data.cert;
        this.$store.state.title = this.$t('own.confirm');
    }
    private cancelOwnSign(){
        this.oShowSign=false;
        this.oShowConfirm=true;

    }
    private ownSignment():void{
        if(this.ownSign){
            this.$router.push({name:'own_process',params:{inputMsg:this.$route.params.inputMsg}})
        }else {
            notes.add({type: NoteType.Error, message: this.$t('notifyMsg.inputSign').toString()});
        }

    }
    private confirmOwnAgin():void{
        this.ownSign='';
        this.oShowConfirm=false;
        this.oShowSign=true;
    }
}
